<template>
	<view class='container padding'>
		<block v-if="userInfo.type==3">
			<view class="title">请选择与您的问题关联的订单<text class="text-red">*</text></view>
			<view class="item_info flex-row-between" @click="relativeOrder">
				<block v-if="relativeOrderInfo.carTypeName">
					<text class="car_type text-ellipsis">{{relativeOrderInfo.carTypeName}}</text>
					<text class="shop text-ellipsis">{{relativeOrderInfo.store4sName}}</text>
				</block>
				<block v-else>
					<text>暂无订单记录，无法进行问题反馈</text>
				</block>
				<view class="icon_w flex-row-center"><image src="../../static/down_arrow.png" class="icon roate"></image></view>
			</view>
		</block>
		
		<view class="title">请选择您的问题类型<text class="text-red">*</text></view>
		<view class="item_info flex-row-between">
			<picker
				@change="orderChange" 
				:value="feedbackTypeIdx"
				:range="questionList" 
				class="picker"
				range-key="name"
			>
				<view class="picker">
					<view :class="[!feedbackTypeIdx?'placeholder_style':'']">
						{{feedbackTypeIdx? questionList[feedbackTypeIdx].name : '请选择您的问题类型'}}
					</view>
				</view>
			</picker>
			<view class="icon_w flex-row-center">
				<image src="../../static/down_arrow.png" class="icon"></image>
			</view>
		</view>
		
		
		<view class="title">请输入您的投诉/建议<text class="text-red">*</text></view>
		<view class="item_info flex-row-between">
			<textarea 
				class="textarea" 
				placeholder="请描述您反馈的问题，最少10个字，最多500字" 
				maxlength="500" 
				auto-height 
				v-model="paramsInfo.problem"
			></textarea>
			<text class="max">{{paramsInfo.problem.length}}/500</text>
		</view>
		
		<view class="title flex-row-start-end">
			上传凭证
			<block v-if="feedbackTypeIdx==4">
				<text class="little">（违规违纪类必须提供凭证）</text>
				<text class="text-red">*</text>
			</block>
		</view>
		<view class="upload_w">
			<view class="upload">
				<view class="item" v-for='(litt,idx) in affixList' :key='idx'>
					<img src="../../static/delete.png" class='delete' @click='deleteFile(litt)'>
					<view @click='lookAffix(litt)' class='inner_w'>
						<image :src="litt.openUrl" class='pic'/>
					</view>
				</view>
				<view class="inner item" @click='takePicture' v-if="affixList.length<4">
					<image src="../../static/pic.png"></image>
				</view>
			</view>
		</view>
		<view class='btn_wrapper'>
			<view class="sure">
				<button class="cu-btn block lg" @click="feedback" :disabled="btnDisabled">
					提交
				</button>
			</view>
		</view>
		<!-- 图片查看 -->
		<view class="cu-modal" :class="lookImg?'show':''" catchtouchmove="return">
			<view class="cu-dialog">
				<view class="bg-img">
					<image :src="currentImgUrl" class="dialogImg" mode="widthFix"></image>
				</view>
				<view class="cu-bar bg-white">
					<view class="action margin-0 flex-sub" @click="lookImg=false">关闭</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {http2Https} from "../../common/tools.js"
	export default {
		data() {
			return {
				text:'',
				feedbackTypeIdx:'',
				questionList:[
					{name:"信息咨询类",id:1},
					{name:"改善建议类",id:2},
					{name:"服务投诉类",id:3},
					{name:"质量投诉类",id:4},
					{name:"违规违纪类",id:5}
				],
				paramsInfo:{
					orderId:'',
					feedbackType:'',//1-信息咨询类、2-改善建议类、3-服务投诉类、4-质量投诉类、5-违规违纪类
					problem:"",
					attUrls:'',
				},
				affixList:[],
				btnDisabled:false,
				relativeOrderInfo:{},//关联订单
				fromType:1 ,//1 客户，2 员工,
				userInfo:{}, //客户信息
				lookImg:false,
				currentImgUrl:''
			};
		},
		methods:{
			orderChange(e){
				var _idx=e.detail.value //选的第几个
				this.feedbackTypeIdx=_idx
				this.paramsInfo.feedbackType=this.questionList[this.feedbackTypeIdx].id
			},
			getDefaultOrderInfo(){
				this.request("/feedBack/impliedOrder").then((res)=>{
					if(res.resultCode==0){
						this.relativeOrderInfo=res.data
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			relativeOrder(){
				let _url="/pages/feedback/relativeOrder"
				if(this.relativeOrderInfo.orderId){
					_url+="?orderId="+this.relativeOrderInfo.orderId
				}
				this.perfectNavigateTo({
					url:_url
				})
			},
			takePicture(){
				var _that=this
				uni.chooseImage({
					count:1,
					sizeType:["compressed"],
					sourceType: ['album', 'camera'], //从相册选择
					success: function (res) {
						let imgPaths=res.tempFilePaths
						for(let i=0;i<imgPaths.length;i++){
							(function(i){
								uni.showLoading()
								uni.uploadFile({
									url:getApp().globalData.requestUrl+"/common/upload",
									filePath:imgPaths[i],
									name: 'upFile',
									header: {
										'content-type': 'multipart/form-data',
										'service-code':'QDMS',
										"ticket":JSON.parse(uni.getStorageSync('token')).value
									},
									success:(res)=>{
										const _data=JSON.parse(res.data)
										let result=_data.data
										_that.affixList.push({
											openUrl:http2Https(result.openUrl),
											fileId:result.fileId
										})
										_that.affixBtnDisabled=false
									},
									complete:(res)=>{
										uni.hideLoading()
									},
								})
							})(i)
						}
					}
				});
			},
			lookAffix(item){
				if(this.mobileModel=="VOG-AL00"){ //华为p30
					this.currentImgUrl=item.openUrl;
					this.lookImg=true
				}else{
					//获取 url数组
					var _urlList=[]
					this.affixList.forEach((item)=>{
						_urlList.push(item.openUrl)
					})
					uni.showLoading()
					uni.previewImage({
						current:item.openUrl,
						urls: _urlList,
						longPressActions:{
							itemList: ['发送给朋友', '保存图片', '收藏']
						},
						fail(err){
							this.currentImgUrl=item.openUrl;
							this.lookImg=true
						},
						complete(){
							uni.hideLoading()
						}
					})
				}
			},
			deleteFile(item){
				this.request("/common/deleteFile",{fileId:item.fileId}).then((res)=>{
					if(res.resultCode==0){
						var _arr=this.affixList
						for(let i=0;i<_arr.length;i++){ //删除 所有附件中的文件
							if(item.fileId==_arr[i].fileId){
								this.affixList.splice(i,1)
								break
							}
						}
						if(this.affixList.length<=0){
							this.affixBtnDisabled=true
						}
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			feedback(){
				var _strArr=[],requrstUrl="/feedBack/staffFeedBack"
				this.affixList.forEach((item)=>{
					_strArr.push(item.openUrl)
				})
				this.paramsInfo.attUrls=String(_strArr)
				if(this.userInfo.type==3){ //只有客户才用关联订单 
					requrstUrl="/feedBack/meberFeedBack"
					this.paramsInfo.orderId=this.relativeOrderInfo.orderId
					if(!this.paramsInfo.orderId){ uni.showToast({title: '请关联订单',icon:'none'});return}
				}else{
					delete this.paramsInfo.orderId
				}
				if(!this.paramsInfo.feedbackType){ uni.showToast({title: '请选择您要反馈的问题类型',icon:'none'});return}
				if(!this.paramsInfo.problem || this.paramsInfo.problem.length<10){ uni.showToast({title: '请描述您反馈的问题，最少10个字，最多500字',icon:'none'});return}
				if(this.feedbackTypeIdx==4){ //如果是投诉，则必须上传附件
					if(!this.paramsInfo.attUrls){
						uni.showToast({title:"请上传违规违纪凭证",icon:"none"});
						return
					}
				}
				this.btnDisabled=true
				this.request(requrstUrl,this.paramsInfo).then((res)=>{
					if(res.resultCode==0){
						uni.showToast({title:res.resultMsg,icon:"success",success(){
							setTimeout(()=>{
								uni.navigateBack({
									delta:1
								})
							},1500)
						}})
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			}
		},
		onLoad(){
			let _userInfo=JSON.parse(uni.getStorageSync('userInfo'));
			this.userInfo=_userInfo
			if(_userInfo.type==3){ //客户
				this.getDefaultOrderInfo()
			}
		},
	}
</script>

<style lang="stylus">
	.container
		padding-bottom:20upx
		.title
			height:44upx;
			font-size:32upx;
			font-weight:600;
			color:rgba(30,30,30,1);
			margin-bottom:45upx
			&:not(:first-child)
				margin-top:60upx
			.little
				color:#8491A8
				font-size:24upx
				font-weight:normal
		.item_info
			min-height:100upx;
			background:rgba(248,249,252,1);
			border-radius:8upx;
			padding:0upx 0upx 0upx 30upx
			position:relative
			.car_type
				width:278upx
				margin-right:40upx
			.shop
				width 198upx
				margin-right:40upx
			.question_type
				margin-right:40upx
			.icon_w
				padding:20upx 0upx 20upx 20upx
				position:absolute
				right:18upx
				top:17upx
				pointer-events none
				.icon
					width:28upx
					height:28upx
					&.roate
						transform rotateZ(-90deg)
			.picker
				flex:1
				height:100upx
				line-height:100upx
			.textarea
				padding:30upx 0upx 50upx 0upx
				width:100%
				font-size:28upx
				min-height:200upx
			.max
				position:absolute
				bottom:10upx
				right:10upx
				color:rgba(178,182,192,1)
				font-size:24upx
		.upload_w
			.upload
				display:flex
				color:#1E1E1E
				flex-direction:row
				justify-content flex-start
				flex-wrap:wrap
				.item
					width:130upx
					height:130upx;
					background:rgba(249,251,252,1);
					border:2upx solid rgba(227,233,240,1);
					margin-bottom:25upx
					display:flex
					flex-direction:row
					align-items:center
					justify-content center
					position:relative
					.delete
						position:absolute
						width:45upx
						height:45upx
						right:-24upx
						top:-24upx
						z-index:2
					.inner_w
						width:100%
						height:100%
						display:flex
						flex-direction:row
						align-items:center
						justify-content center
						.img
							width:48upx
							height:48upx
						.pic
							width:100%
							height:100%
					&:not(:last-child)
						margin-right:4%
					&.inner
						display:flex
						flex-direction row
						align-items center
						image
							width:48upx
							height:48upx
		.btn_wrapper
			background:#FFFFFF
			height:155upx
			margin-top:60upx
			.lg
				height:112upx
			.sure
				button
					border-radius:16upx;
					color:#fff
					background:rgba(31,35,52,1);
</style>
